function getFileIcon(type: string, size: "sm" | "md" | "lg" = "md") {
  switch(type) {
    case 'PDF':
      return <PDFIcon size={size} />;
    case 'Word':
      return <WordIcon size={size} />;
    case 'Excel':
      return <ExcelIcon size={size} />;
    case 'PowerPoint':
      return <PowerPointIcon size={size} />;
    case 'Image':
      return <ImageIcon size={size} />;
    default:
      return <GenericDocumentIcon size={size} />;
  }
}

function generateFileSize() {
  const sizes = ['KB', 'MB'];
  const size = (Math.random() * 5000 + 100).toFixed(0);
  const unit = sizes[Math.floor(Math.random() * sizes.length)];
  return `${size} ${unit}`;
}

/* =======================
   Page
======================= */

export default function DocumentsPage() {
  const [comparisons, setComparisons] = useState<ComparisonItem[]>([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState("");
  const [viewMode, setViewMode] = useState<ViewMode>("detail");
  const [sortBy, setSortBy] = useState("date");
  const [showUploadModal, setShowUploadModal] = useState(false);

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    setLoading(true);
    try {
      const res = await fetch(`${API_BASE}/comparisons?limit=300`);
      const data = await res.json();
      setComparisons(Array.isArray(data) ? data : []);
    } catch (error) {
      console.error("Failed to fetch data:", error);
    } finally {
      setLoading(false);
    }
  };

  const handleUploadComplete = () => {
    fetchData(); // Refresh data after upload
  };

  /* =======================
     Build documents
  ======================= */
  const documents = useMemo<DocumentCard[]>(() => {
    const map = new Map<string, DocumentCard>();

    comparisons.forEach((c) => {
      const name = (c.document_name || "Untitled").trim();
      const fileType = getFileType(name);

      if (!map.has(name)) {
        map.set(name, {
          id: encodeURIComponent(name),
          name,
          risk: normalizeRisk(c.overall_risk_level),
          latestVersion: c.version_new_label || "1.0",
          lastUpdated: c.created_at,
          totalComparisons: 0,
          fileType,
          fileSize: generateFileSize(),
        });
      }

      const doc = map.get(name)!;
      doc.totalComparisons++;

      if (new Date(c.created_at) > new Date(doc.lastUpdated)) {
        doc.lastUpdated = c.created_at;
        doc.latestVersion = c.version_new_label || doc.latestVersion;
      }

      const r = normalizeRisk(c.overall_risk_level);
      if (riskRank[r] > riskRank[doc.risk]) doc.risk = r;
    });

    return Array.from(map.values());
  }, [comparisons]);

  const sortedDocuments = useMemo(() => {
    const docs = [...documents];
    switch(sortBy) {
      case "name":
        return docs.sort((a, b) => a.name.localeCompare(b.name));
      case "date":
        return docs.sort((a, b) => new Date(b.lastUpdated).getTime() - new Date(a.lastUpdated).getTime());
      case "type":
        return docs.sort((a, b) => a.fileType.localeCompare(b.fileType));
      case "risk":
        return docs.sort((a, b) => riskRank[b.risk] - riskRank[a.risk]);
      default:
        return docs;
    }
  }, [documents, sortBy]);

  const filtered = sortedDocuments.filter((d) =>
    d.name.toLowerCase().includes(search.toLowerCase())
  );

  /* =======================
     Statistics
  ======================= */
  const stats = useMemo(() => {
    const total = documents.length;
    const lowRisk = documents.filter(d => d.risk === "LOW").length;
    const mediumRisk = documents.filter(d => d.risk === "MEDIUM").length;
    const highRisk = documents.filter(d => d.risk === "HIGH").length;
    
    return { total, lowRisk, mediumRisk, highRisk };
  }, [documents]);

  /* =======================
     Handle row click for Detail View
  ======================= */
  const handleRowClick = (docName: string) => {
    window.location.href = `/history?document_name=${encodeURIComponent(docName)}`;
  };

  const handleCompareClick = (e: React.MouseEvent, docName: string) => {
    e.stopPropagation();
    window.location.href = `/compare?document_name=${encodeURIComponent(docName)}`;
  };

  /* =======================
     Get icon size based on view mode
  ======================= */
  const getIconSizeForView = () => {
    switch(viewMode) {
      case "grid": return "md";
      case "list": return "sm";
      case "detail": return "sm";
      default: return "md";
    }
  };

  /* =======================
     UI
  ======================= */

  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-50 to-gray-100 pb-16">
      {/* Header */}
      <div className="bg-white border-b shadow-sm">
        <div className="max-w-full mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-xl font-bold text-gray-900">üìÅ Document Management System</h1>
              <p className="text-gray-600 text-sm mt-1">
                Total: {stats.total} documents ‚Ä¢ Safe: {stats.lowRisk} ‚Ä¢ High Risk: {stats.highRisk}
              </p>
            </div>
            <div className="flex items-center gap-4">
              <div className="relative">
                <MagnifyingGlassIcon className="h-5 w-5 text-gray-400 absolute left-3 top-1/2 -translate-y-1/2" />
                <input
                  value={search}
                  onChange={(e) => setSearch(e.target.value)}
                  placeholder="Search documents..."
                  className="pl-10 pr-4 py-2 text-sm border border-gray-300 rounded-lg bg-white focus:ring-2 focus:ring-blue-500 focus:border-blue-500 w-64 transition-all"
                />
              </div>
              <UploadButton onClick={() => setShowUploadModal(true)} />
              <button
                onClick={fetchData}
                className="p-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors active:scale-95"
                title="Refresh data"
              >
                <ArrowPathIcon className={`h-5 w-5 text-gray-600 ${loading ? 'animate-spin' : ''}`} />
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Toolbar */}
      <div className="bg-white border-b px-4 py-3 shadow-sm">
        <div className="max-w-full mx-auto flex items-center justify-between">
          <div className="flex items-center gap-4">
            <div className="flex items-center border border-gray-300 rounded-lg overflow-hidden bg-gray-50">
              <button
                onClick={() => setViewMode("grid")}
                className={`p-2 transition-colors ${viewMode === "grid" ? "bg-blue-100 text-blue-600 border-r border-gray-300" : "hover:bg-gray-100 text-gray-600"}`}
                title="Grid view"
              >
                <Squares2X2Icon className="h-4 w-4" />
              </button>
              <button
                onClick={() => setViewMode("list")}
                className={`p-2 transition-colors ${viewMode === "list" ? "bg-blue-100 text-blue-600 border-r border-gray-300" : "hover:bg-gray-100 text-gray-600"}`}
                title="List view"
              >
                <Bars3BottomLeftIcon className="h-4 w-4" />
              </button>
              <button
                onClick={() => setViewMode("detail")}
                className={`p-2 transition-colors ${viewMode === "detail" ? "bg-blue-100 text-blue-600" : "hover:bg-gray-100 text-gray-600"}`}
                title="Detail view"
              >
                <ViewColumnsIcon className="h-4 w-4" />
              </button>
            </div>
            
            <select 
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value)}
              className="px-3 py-2 text-sm border border-gray-300 rounded-lg bg-white focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-colors"
            >
              <option value="date">Sort by Date</option>
              <option value="name">Sort by Name</option>
              <option value="type">Sort by Type</option>
              <option value="risk">Sort by Risk</option>
            </select>
          </div>
          
          <div className="text-sm text-gray-600 font-medium">
            Showing <span className="text-blue-600">{filtered.length}</span> of <span className="text-gray-900">{documents.length}</span> items
          </div>
        </div>
      </div>

      {/* Content Area */}
      <div className="p-4">
        {/* Loading State */}
        {loading && (
          <div className="flex flex-col items-center justify-center h-64">
            <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500 mb-4"></div>
            <span className="text-gray-600">Loading documents...</span>
          </div>
        )}

        {/* Empty State */}
        {!loading && filtered.length === 0 && (
          <div className="flex flex-col items-center justify-center h-64 bg-white rounded-xl border border-gray-200 shadow-sm">
            <div className="relative h-16 w-14 mb-6">
              <div className="absolute inset-0 bg-gradient-to-b from-gray-400 to-gray-600 rounded-tl-sm rounded-tr-md rounded-bl-sm rounded-br-md shadow-md">
                <div className="absolute top-0 right-0">
                  <div className="w-0 h-0 border-l-[20px] border-l-transparent border-b-[20px] border-b-gray-700" />
                </div>
                <div className="absolute inset-0 flex flex-col items-center justify-center space-y-0.5 pt-3">
                  <div className="w-3/4 h-1 bg-gray-300/40 rounded-full"></div>
                  <div className="w-4/5 h-1 bg-gray-300/30 rounded-full"></div>
                  <div className="w-2/3 h-1 bg-gray-300/20 rounded-full"></div>
                </div>
                <div className="absolute bottom-2 left-0 right-0">
                  <div className="text-white text-[8px] font-medium text-center">NO FILES</div>
                </div>
              </div>
            </div>
            <h3 className="text-lg font-semibold text-gray-900">No documents found</h3>
            <p className="mt-1 text-gray-500 text-sm">
              {search ? "Try searching with different keywords" : "No documents in the system yet"}
            </p>
            <div className="flex gap-3 mt-6">
              <button
                onClick={fetchData}
                className="px-5 py-2.5 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-all shadow-sm hover:shadow-md"
              >
                ‚Üª Refresh Data
              </button>
              <button
                onClick={() => setShowUploadModal(true)}
                className="px-5 py-2.5 bg-gradient-to-r from-green-500 to-green-600 text-white rounded-lg hover:from-green-600 hover:to-green-700 transition-all shadow-sm hover:shadow-md"
              >
                üì§ Upload First Document
              </button>
            </div>
          </div>
        )}

        {/* Grid View */}
        {!loading && viewMode === "grid" && (
          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 xl:grid-cols-6 gap-5">
            {filtered.map((doc) => (
              <Link
                key={doc.id}
                href={`/history?document_name=${encodeURIComponent(doc.name)}`}
                className="group bg-white border border-gray-200 rounded-xl p-5 hover:shadow-lg transition-all duration-300 hover:border-blue-300 hover:-translate-y-0.5"
              >
                <div className="flex flex-col items-center text-center">
                  <div className="mb-4 relative">
                    {getFileIcon(doc.fileType, "md")}
                    <div className={`absolute -bottom-1 -right-1 h-3.5 w-3.5 rounded-full border-2 border-white shadow-sm ${
                      doc.risk === "HIGH" ? "bg-gradient-to-br from-red-500 to-red-600" :
                      doc.risk === "MEDIUM" ? "bg-gradient-to-br from-amber-500 to-amber-600" :
                      "bg-gradient-to-br from-green-500 to-green-600"
                    }`} />
                  </div>
                  <div className="w-full">
                    <div className="font-semibold text-sm truncate group-hover:text-blue-600 transition-colors" title={doc.name}>
                      {doc.name}
                    </div>
                    <div className="text-xs text-gray-500 mt-3 space-y-2">
                      <div className="font-bold text-gray-700">{doc.fileType}</div>
                      <div className="flex items-center justify-center gap-2 text-[11px]">
                        <span>v{doc.latestVersion}</span>
                        <span className="text-gray-300">‚Ä¢</span>
                        <span>{formatDate(doc.lastUpdated)}</span>
                      </div>
                      <div className="flex items-center justify-center gap-3 mt-3">
                        <span className={`px-3 py-1 rounded-full text-[11px] font-semibold shadow-sm ${
                          doc.risk === "HIGH" ? "bg-red-50 text-red-700 border border-red-100" :
                          doc.risk === "MEDIUM" ? "bg-amber-50 text-amber-700 border border-amber-100" :
                          "bg-green-50 text-green-700 border border-green-100"
                        }`}>
                          {doc.risk}
                        </span>
                        <span className="text-gray-400">‚Ä¢</span>
                        <span className="font-medium">{doc.totalComparisons} comp</span>
                      </div>
                    </div>
                  </div>
                </div>
              </Link>
            ))}
          </div>
        )}

        {/* List View */}
        {!loading && viewMode === "list" && (
          <div className="bg-white rounded-xl border border-gray-200 overflow-hidden shadow-sm">
            {filtered.map((doc) => (
              <Link
                key={doc.id}
                href={`/history?document_name=${encodeURIComponent(doc.name)}`}
                className="flex items-center p-5 border-b border-gray-100 last:border-b-0 hover:bg-gradient-to-r hover:from-blue-50/50 hover:to-white group transition-all duration-200"
              >
                <div className="mr-5">
                  {getFileIcon(doc.fileType, "sm")}
                </div>
                <div className="flex-1 min-w-0">
                  <div className="flex items-center justify-between">
                    <div className="font-semibold truncate group-hover:text-blue-600 transition-colors">
                      {doc.name}
                    </div>
                    <div className="text-sm text-gray-500 font-medium ml-4">
                      {doc.fileSize}
                    </div>
                  </div>
                  <div className="flex items-center gap-5 mt-2 text-sm text-gray-600">
                    <span className="font-bold text-gray-700">{doc.fileType}</span>
                    <span className="text-gray-300">‚Ä¢</span>
                    <span>Version {doc.latestVersion}</span>
                    <span className="text-gray-300">‚Ä¢</span>
                    <span>{doc.totalComparisons} comparisons</span>
                    <span className="text-gray-300">‚Ä¢</span>
                    <span className="text-gray-500">{formatDate(doc.lastUpdated)}</span>
                  </div>
                </div>
                <div className="ml-5">
                  <span className={`px-4 py-1.5 rounded-full text-xs font-semibold shadow-sm ${
                    doc.risk === "HIGH" ? "bg-red-50 text-red-700 border border-red-100" :
                    doc.risk === "MEDIUM" ? "bg-amber-50 text-amber-700 border border-amber-100" :
                    "bg-green-50 text-green-700 border border-green-100"
                  }`}>
                    {doc.risk}
                  </span>
                </div>
              </Link>
            ))}
          </div>
        )}

        {/* Detail View */}
        {!loading && viewMode === "detail" && (
          <div className="bg-white rounded-xl border border-gray-200 overflow-hidden shadow-sm">
            {/* Table Header */}
            <div className="grid grid-cols-12 gap-4 px-7 py-4 text-sm font-semibold text-gray-700 border-b border-gray-100 bg-gradient-to-r from-gray-50 to-gray-100/50">
              <div className="col-span-5 flex items-center gap-3">
                <span>Name</span>
              </div>
              <div className="col-span-2">Last Modified</div>
              <div className="col-span-2">Type</div>
              <div className="col-span-1">Size</div>
              <div className="col-span-2">Risk Level</div>
            </div>

            {/* Table Rows */}
            {filtered.map((doc) => (
              <div
                key={doc.id}
                onClick={() => handleRowClick(doc.name)}
                className="grid grid-cols-12 gap-4 px-7 py-5 border-b border-gray-100 last:border-b-0 hover:bg-gradient-to-r hover:from-blue-50/50 hover:to-white group cursor-pointer transition-all duration-200 active:bg-blue-100/30"
              >
                <div className="col-span-5 flex items-center">
                  <div className="mr-4">
                    {getFileIcon(doc.fileType, "sm")}
                  </div>
                  <div className="min-w-0">
                    <div className="font-semibold truncate group-hover:text-blue-600 transition-colors">
                      {doc.name}
                    </div>
                    <div className="text-sm text-gray-500 mt-1">
                      Version {doc.latestVersion} ‚Ä¢ {doc.totalComparisons} comparisons
                    </div>
                  </div>
                </div>
                <div className="col-span-2 flex items-center text-sm text-gray-600">
                  {formatDate(doc.lastUpdated)}
                </div>
                <div className="col-span-2 flex items-center">
                  <span className="text-sm font-bold text-gray-700">{doc.fileType}</span>
                </div>
                <div className="col-span-1 flex items-center text-sm text-gray-600 font-medium">
                  {doc.fileSize}
                </div>
                <div className="col-span-2 flex items-center">
                  <div className="flex items-center gap-4">
                    <span className={`px-4 py-1.5 rounded-full text-xs font-semibold shadow-sm ${
                      doc.risk === "HIGH" ? "bg-red-50 text-red-700 border border-red-100" :
                      doc.risk === "MEDIUM" ? "bg-amber-50 text-amber-700 border border-amber-100" :
                      "bg-green-50 text-green-700 border border-green-100"
                    }`}>
                      {doc.risk}
                    </span>
                    <div className="flex gap-1 opacity-0 group-hover:opacity-100 transition-opacity duration-300">
                      <button
                        onClick={(e) => handleCompareClick(e, doc.name)}
                        className="p-1.5 hover:bg-gray-200 rounded-lg transition-colors active:scale-95"
                        title="Compare"
                      >
                        <InformationCircleIcon className="h-4 w-4 text-gray-600" />
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Status Bar */}
      {!loading && (
        <div className="fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 px-5 py-3 text-sm text-gray-600 shadow-lg">
          <div className="flex justify-between items-center">
            <div className="font-medium">
              <span className="text-gray-900">{filtered.length}</span> items displayed
              <span className="mx-3">‚Ä¢</span>
              <button 
                onClick={() => setShowUploadModal(true)}
                className="text-blue-600 hover:text-blue-800 font-medium"
              >
                + Upload new documents
              </button>
            </div>
            <div className="flex items-center gap-8">
              <div className="flex items-center gap-3">
                <div className="h-3.5 w-3.5 rounded-full bg-gradient-to-br from-green-500 to-green-600 shadow-sm"></div>
                <span className="font-medium">Safe: <span className="text-gray-900">{stats.lowRisk}</span></span>
              </div>
              <div className="flex items-center gap-3">
                <div className="h-3.5 w-3.5 rounded-full bg-gradient-to-br from-amber-500 to-amber-600 shadow-sm"></div>
                <span className="font-medium">Medium Risk: <span className="text-gray-900">{stats.mediumRisk}</span></span>
              </div>
              <div className="flex items-center gap-3">
                <div className="h-3.5 w-3.5 rounded-full bg-gradient-to-br from-red-500 to-red-600 shadow-sm"></div>
                <span className="font-medium">High Risk: <span className="text-gray-900">{stats.highRisk}</span></span>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Upload Modal */}
      <UploadModal 
        isOpen={showUploadModal}
        onClose={() => setShowUploadModal(false)}
        onUploadComplete={handleUploadComplete}
      />
    </div>
  );
}